<template>
  <div style="background-color:#f6f6f6;font-size:15px;">
    <div class="title">{{ $route.query.orderRoomUserId?'请输入身份信息':'身份信息' }}</div>
    <div v-if="$route.query.orderRoomUserId">
      <van-field
        label="姓名"
        placeholder="请输入入住人真实姓名"
        v-model="username"
      ></van-field>
      <van-field
        v-model="IDcard"
        label="身份证号"
        maxlength="18"
        placeholder="请输入身份证号"
      />
    </div>
    <div v-else>
      <van-field
        label="姓名"
        v-model="username"
        readonly
      ></van-field>
      <van-field
        v-model="IDcard"
        label="身份证号"
        readonly
      />
    </div>
    <div
      class="ok"
      @click="sure"
      v-if="$route.query.orderRoomUserId"
    >确认添加</div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { PostBusinessOrderroomuserAdd } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            username: '',
            IDcard: ''
        };
    },
    created() {
        if (this.$route.query.orderRoomUserId) {
            this.$store.commit('setHeader', '添加入住人');
        } else {
            this.$store.commit('setHeader', '入住人信息');
            this.username = this.$route.query.realName;
            this.IDcard = this.$route.query.idenIds;
        }
    },
    methods: {
        sure() {
            if (!this.username) {
                Toast({
                    message: '请输入入住人真实姓名',
                    duration: 900
                });
                this.username = '';
                return;
            }
            if (!this.IDcard || (this.IDcard && !/^[1-9]{1}[0-9]{14}$|^[1-9]{1}[0-9]{16}([0-9]|[xX])$/.test(this.IDcard))) {
                Toast({
                    message: '请正确填写身份证号码',
                    duration: 900
                });
                this.IDcard = '';
                return false;
            }
            let arr = [];
            let obj = {
                roomUser: this.username,
                idCard: this.IDcard
            };
            arr.push(obj);
            let data = {
                orderRoomId: this.$route.query.orderRoomUserId,
                roomUser: JSON.stringify(arr)
            };
            PostBusinessOrderroomuserAdd(data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(-1)', 900);
            });
        }
    }
};
</script>
<style scoped>
.title {
    width: 100%;
    text-align: left;
    color: #888;
    display: inline-block;
    margin: 10px;
    font-size: 0.8rem;
}
.ok {
    width: 90%;
    background: #fb9e58;
    color: #fff;
    line-height: 2.8rem;
    border-radius: 5px;
    margin: 20px auto;
}
/deep/ .van-field__label {
    text-align: left;
}
</style>



